.step{
    background-image: url('../images/backgrounds/breadcrumb-bg.svg');
    background-size: cover;
    background-position: center center;
    padding: 200px 0;
    .section__title{
        margin-bottom: 100px;
    }
    &__item{
        background: $white;
        padding: 50px 30px 30px;
        border-radius: 6px;
        box-shadow: $main-shadow;
        position: relative;
        margin-bottom: 50px;
        @include desktop{
            padding: 60px 30px 30px;
        }
        .number{
            position: absolute;
            font-size: 40px;
            width: 80px; height: 80px;
            display: block;
            background: $white;
            text-align: center;
            line-height: 80px;
            box-shadow: $main-shadow;
            border-radius: 100%;
            top: -30px; left: -30px;
            font-family: $primary-font;
            font-weight: 700;
            color: #9795f0;
            @include tablet{
                left: 50%;
                transform: translateX(-50%);
            }
        }
        h3{
            margin-bottom: 30px;
        }
        p{
            font-size: 14px;
        }
        @include tablet{
            text-align: center;
        }
    }
    .trialbox{
        padding: 50px 20px;
        background: $white;
        border-radius: 6px;
        box-shadow: $main-shadow;
    }
}