.service {
    padding: 100px 0;
    &__item {
        padding: 50px 0;
        overflow: hidden;
    }
    &__content {
        h3 {
            margin-bottom: 30px;
        }

        p {
            margin-bottom: 30px;
        }
        @include desktop{
            margin-bottom: 70px;
            text-align: center;
        }
    }

    &__image {
        position: relative;
        height: 500px;
        @include mobile{
            height: 400px;
        }
        .image {
            position: absolute;
        }
        &.followers {
            .right{
                right: 0; top: 20px;
                z-index: 1;
                animation: left_anim 6s ease-in infinite alternate-reverse;
                img{
                    width: 350px;
                    @include mobile{
                        width: 270px;
                    }
                }
            }
            .left{
                left: 0; bottom: 20px;
                z-index: 1;
                animation: right_anim 5s ease-in infinite alternate-reverse;
                img{
                    width: 350px;
                    @include mobile{
                        width: 270px;
                    }
                }
            }
            .middle{
                left: 50%; top: 50%;
                transform: translate(-50%, -50%);
                img{
                    width: 420px;
                    @include mobile{
                        width: 350px;
                    }
                }
            }
        }
        
        &.likes {
            .right{
                right: 0; bottom: 20px;
                z-index: 1;
                img{
                    width: 350px;
                    @include mobile{
                        width: 270px;
                    }
                }
                animation: left_anim 6s ease-in infinite alternate-reverse;
            }
            .left{
                left: 0; top: 68px;
                z-index: 1;
                img{
                    width: 350px;
                    @include mobile{
                        width: 270px;
                    }
                }
                animation: right_anim 5s ease-in infinite alternate-reverse;
            }
            .middle{
                left: 50%; top: 50%;
                transform: translate(-50%, -50%);
                img{
                    width: 420px;
                    @include mobile{
                        width: 350px;
                    }
                }
            }
        }
        &.views {
            .right{
                right: 0; bottom: 20px;
                z-index: 1;
                @include mobile{
                    bottom: 40px;
                }
                img{
                    width: 350px;
                    @include mobile{
                        width: 270px;
                    }
                }
                animation: left_anim 6s ease-in infinite alternate-reverse;
            }
            .left{
                left: 0; top: 68px;
                z-index: 1;
                @include mobile{
                    top: 80px;
                }
                img{
                    width: 350px;
                    @include mobile{
                        width: 270px;
                    }
                }
                animation: right_anim 5s ease-in infinite alternate-reverse;
            }
            .middle{
                left: 50%; top: 50%;
                transform: translate(-50%, -50%);
                img{
                    width: 420px;
                    @include mobile{
                        width: 350px;
                    }
                }
            }
        }
        &.a-likes {
            .right-top{
                right: 0; top: -20px;
                z-index: 1;
                img{
                    width: 150px;
                }
                animation: updown 3s ease-in-out infinite alternate-reverse;
            }
            .right-bottom{
                right: 0; bottom: 20px;
                z-index: 1;
                img{
                    width: 150px;
                }
                animation: updown 4s ease-in-out infinite alternate-reverse;
            }
            .left{
                left: 0; top: 68px;
                z-index: 1;
                img{
                    width: 150px;
                }
                animation: updown 2s ease-in-out infinite alternate-reverse;
            }
            .middle{
                left: 50%; top: 50%;
                transform: translate(-50%, -50%);
                img{
                    width: 420px;
                    @include mobile{
                        width: 350px;
                    }
                }
            }
        }
    }

    &__title-secondary {
        position: relative;
        &::before {
            content: '';
            position: absolute;
            width: 80px;
            height: 10px;
            background-color: $secondary-color;
            z-index: -1;
            border-radius: 10px;
            bottom: -15px;
            left: 0;
            @include desktop{
                left: 50%;
                transform: translateX(-50%);
            }
        }
    }

    &__title-primary {
        position: relative;

        &::before {
            content: '';
            position: absolute;
            width: 80px;
            height: 10px;
            background-color: $primary-color;
            z-index: -1;
            border-radius: 10px;
            bottom: -15px;
            right: 0;
            @include desktop{
                left: 50%;
                transform: translateX(-50%);
            }
        }
    }
}

@keyframes left_anim{
    0% {
        transform: translateX(0);
    }
    100%{
        transform: translateX(-50px);
    }
}
@keyframes right_anim{
    0% {
        transform: translateX(0);
    }
    100%{
        transform: translateX(50px);
    }
}

@keyframes updown{
    0% {
        transform: translateY(0);
    }
    100%{
        transform: translateY(20px);
    }
}

/* ==================================================================================
New Styles from here
===================================================================================== */

.features__item {
    margin-bottom: 100px;
}

.features__image {
    height: 250px;
    position: relative;
    margin: auto;
    margin-bottom: 20px;
    .illustration{
        position: absolute;
        &.top-left{
            left: 0; top: 20px;
            z-index: 1;
            animation: right_anim 6s ease-in infinite alternate-reverse;
        }
        &.center{
            top: 50%; left: 50%;
            transform: translate(-50%, -50%);
            width: 250px;
        }
        &.bottom-right{
            right: 0; bottom: -40px;
            z-index: 1;
            animation: left_anim 6s ease-in infinite alternate-reverse;
        }
    }
}

.delay-dot {
    left: -20px;
    bottom: -10px;
}

.support-dot {
    left: -20px;
    top: 30px;
}

.support-dot-2 {
    right: -20px;
    bottom: -10px;
}

.instant-dot {
    bottom: 0px;
    right: -20px;
}

.testimonial {
    padding: 200px 0;
    &__slider {
        border-radius: 10px;
        background: $white;
        box-shadow: $main-shadow;
        padding: 25px 40px;
        margin-bottom: 30px;
        .prevArrow,
        .nextArrow {
            color: $white;
            font-size: 40px;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            border: 0;
            background: transparent;
            outline: 0;
            cursor: pointer;
        }

        .prevArrow {
            left: -50px;
        }

        .nextArrow {
            right: -50px;
        }
    }

    &__slides {
        outline: 0;

        h6 {
            margin-bottom: 10px;
        }

        p {
            margin-bottom: 10px;
        }

        ul {
            li {
                i {
                    color: $primary-color;
                    font-size: 16px;
                }
            }
        }

        span {
            color: $text-color-light;
        }
    }
}

.rating-star {
    li {
        margin: 0 10px;
        i {
            color: $white;
            font-size: 40px;
        }
    }
}

.buy__steps {
    background: $white;
    text-align: center;
    padding: 40px 10px;
    box-shadow: $main-shadow;
    border-radius: 10px;
    margin-bottom: 50px;
    img{
        margin-top: 15px;
        width: 40px;
    }
}

.expect__list {
    li {
        margin: 0 25px;
        @include tablet{
            margin-bottom: 20px;
        }
    }

    img {
        height: 30px;
        width: auto;
    }
}

.our__service {
    margin-bottom: 50px;

    h4 {
        margin-bottom: 20px;
    }
}

.pricing__item {
    background: $white;
    text-align: center;
    padding: 100px 50px;
    box-shadow: $main-shadow;
    margin-bottom: 50px;
    position: relative;
    border-radius: 4px;
    @include desktop{
        padding: 70px 30px;
    }
    &-badge{
        position: absolute;
        top: 30px; right: 30px;
        background: $secondary-color;
        padding: 12px 20px;
        border-radius: 6px;
        @include desktop{
            top: 10px; right: 10px;
        }
        span{
            color: $white;
            font-size: 12px;
            font-family: $primary-font;
            margin-top: 0;
            font-weight: 600;
        }
    }
    h4 {
        margin-bottom: 40px;

        span {
            font-weight: 300;
        }
    }

    h2 {
        margin-bottom: 50px;

        span {
            font-size: 25px;
            font-weight: 300;
            vertical-align: text-top;
        }
    }

    h6 {
        margin-top: -31px;
    }

    ul {
        margin-bottom: 50px;

        li {
            list-style-type: none;
            margin: 10px 0;
        }
    }
}