.hero__area{
    padding: 200px 0 150px;
    background-image: url('../images/backgrounds/hero-bg.svg');
    background-size: cover;
    background-position: bottom center;
    position: relative;
    @include desktop{
        padding: 150px 0 100px;
    }
    &-content{
        h2, p{
            color: $white;
        }
        h2{
            margin-bottom: 20px;
        }
        p{
            width: 70%;
            margin-bottom: 30px;
            @include desktop{
                margin: 0 auto 30px;
            }
            @include tablet{
                width: 90%;
            }
        }
        @include desktop{
            margin-bottom: 30px;
            text-align: center;
        }
    }
    &-images{
        position: relative;
        height: 580px;
        @include tablet{
            height: 500px;
        }
        .floating-illu{
            position: absolute;
        }
        .large{
            bottom: 0;
            left: 0;
            svg{
                width: 320px;
                height: auto;
                @include tablet{
                    width: 280px;
                }
                @include mobile{
                    width: 240px;
                }
            }
            animation: updown_illustration 4s ease-in infinite alternate-reverse;
        }
        .medium{
            left: 50%;
            transform: translateX(-50%);
            top: 0;
            animation: updown_illustration_middle 5s ease-in infinite alternate-reverse;
            svg{
                width: 370px;
                height: auto;
                @include tablet{
                    width: 310px;
                }
                @include mobile{
                    width: 260px;
                }
            }
        }
        .small{
            right: 0; bottom: -30px;
            animation: updown_illustration 3s ease-in infinite alternate-reverse;
            svg{
                width: 320px;
                @include tablet{
                    width: 280px;
                }
                @include mobile{
                    width: 240px;
                }
            }
        }
        &.likes{
            .back{
                top: 50%; left: 50%;
                transform: translate(-50%, -50%);
                svg{
                    width: 600px;
                    @include desktop-lg{
                        width: 500px;
                    }
                    .love{
                        animation: scale_updown 3s ease-in infinite alternate-reverse;
                        &:nth-child(2n){
                            animation-delay: 2s;
                        }
                    }
                }
                @include tablet{
                    display: none;
                }
            }
            .top-left{
                left: 0; top: 40px;
                z-index: 1;
                img{
                    width: 350px;
                    @include mobile-xs{
                        width: 250px;
                    }
                }
                animation: right_anim 6s ease-in infinite alternate-reverse;
            }
            .center{
                top: 50%; left: 50%;
                transform: translate(-50%, -50%);
                animation: updown_illustration_center 5s ease-in infinite alternate-reverse;
                img{
                    width: 400px;
                    @include mobile{
                        width: 350px;
                    }
                    @include mobile-xs{
                        width: 300px;
                    }
                }
            }
            .bottom-right{
                right: 0; bottom: 40px;
                z-index: 1;
                img{
                    width: 350px;
                    @include mobile-xs{
                        width: 250px;
                    }
                }
                animation: left_anim 6s ease-in infinite alternate-reverse;
            }
        }
        &.automatic{
            .top-left{
                left: 40px; top: 40px;
                z-index: 1;
                @include mobile{
                    left: 0;
                }
                img{
                    width: 180px;
                    @include mobile-xs{
                        width: 100px;
                    }
                }
                animation: updown_illustration 5s ease-in infinite alternate-reverse;
            }
            .top-right{
                right: 70px; top: 60px;
                z-index: 1;
                @include mobile{
                    right: 0;
                }
                img{
                    width: 180px;
                    @include mobile-xs{
                        width: 100px;
                    }
                }
                animation: updown_illustration 3s ease-in infinite alternate-reverse;
            }
            .bottom-right{
                right: 40px; bottom: 40px;
                z-index: 1;
                @include mobile{
                    right: 0;
                }
                img{
                    width: 180px;
                    @include mobile-xs{
                        width: 100px;
                    }
                }
                animation: updown_illustration 6s ease-in infinite alternate-reverse;
            }
        }
    }
    .scroll__down{
        position: absolute;
        left: 50%; transform: translateX(-50%);
        bottom: 40px;
        z-index: 2;
        text-align: center;
        &:hover{
            text-decoration: none;
        }
        i{
            font-size: 30px;
            color: $white;
            animation: updown .8s ease-in infinite alternate-reverse;
        }
        span{
            color: $white;
            font-size: 10px;
            text-transform: uppercase;
            font-family: $primary-font;
            letter-spacing: 3px;
        }
        span, i{
            display: block;
        }
    }
}

@keyframes updown{
    0% {
        transform: translateY(0);
    }
    100%{
        transform: translateY(10px);
    }
}

@keyframes updown_illustration{
    0% {
        transform: translateY(0);
    }
    100%{
        transform: translateY(15px);
    }
}

@keyframes updown_illustration_middle{
    0% {
        transform: translate(-50%, 0);
    }
    100%{
        transform: translate(-50%, 15px);
    }
}

@keyframes updown_illustration_center{
    0% {
        transform: translate(-50%, -50%);
    }
    100%{
        transform: translate(-50%, calc(-50% + 15px));
    }
}

@keyframes scale_updown{
    0% {
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}