.main-nav{
    padding: 25px 30px;
    transition: .3s ease .02s;
    @include desktop{
        background-color: #A59CEC;
    }
    .navbar-nav{
        .nav-item{
            .nav-link{
                color: $white;
                padding-left: 20px;
                padding-right: 20px;
                font-size: 15px;
                text-transform: capitalize;
                letter-spacing: 1px;
                position: relative;
                z-index: 1;
                font-family: $primary-font;
                font-weight: 500;
            }
        }
        @include desktop{
            text-align: center;
        }
    }
    .navbar-brand{
        margin-right: 0;
        text-align: center;
        img{
            max-width: 150px;
        }
    }
    .dropdown{
        .dropdown-menu {
            padding: 10px;
            border: none;
            border-radius: 4px;
            max-width: 350px;
            box-shadow: 4px 13px 25px 1px rgba(0,0,0,.1);
            .dropdown-item{
                font-family: $primary-font;
                color: $text-color;
                padding: 10px 20px;
                font-size: 15px;
                font-weight: 500;
                &:active{
                    background: $white;
                }
            }
            display: block;
            transform: scaleY(0);
            opacity: 0;
            transition: .3s ease;
            transform-origin: top center;
            @include tablet{
                left: 50%;
                max-width: unset;
                text-align: center;
                height: 0;
                padding: 0;
            }
        }
        &:hover{
            .dropdown-menu{
                transform: scaleY(1);
                opacity: 1;
                @include tablet{
                    height: auto;
                    padding: 10px;
                    margin-bottom: 10px;
                }
            }
        }
    }
    .navbar-toggler{
        outline: 0;
        color: $white;
        cursor: pointer;
    }
    &.nav-bg{
        background: #A59CEC;
        padding: 20px;
        border: none;
        box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.09);
    }
}

// Headroomjs Styles
.headroom {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transition: all .8s ease-in-out;
}
.headroom--unpinned {
    top: -500px;
    @include desktop{
        top: 0;
    }
}
.headroom--pinned {
    top: 0;
}